// utils/productPriceCalculator.js
import { calculateFinalPrice } from "./price";

// Cache for storing calculated prices
const priceCache = new Map();

/**
 * Calculate product price from description
 */
export const calculateProductPrice = async (
  description,
  selectedKarat = "10K"
) => {
  try {
    // Create cache key
    const cacheKey = `${description.substring(0, 100)}-${selectedKarat}`;

    // Check cache first
    if (priceCache.has(cacheKey)) {
      return priceCache.get(cacheKey);
    }

    // Use regex to extract specific values from the concatenated text
    const diamondShapeMatch = description.match(
      /Diamond Shape:\s*([^\s]+(?:,\s*[^\s]+)*)/
    );
    const totalDiamondsMatch = description.match(
      /Total Diamonds:\s*([\d,\s]+)/
    );
    const diamondWeightMatch = description.match(
      /Diamond Weight:\s*([\d.,\s]+)/
    );
    const goldWeightMatch = description.match(
      new RegExp(`${selectedKarat} Gold:\\s*([\\d.]+)g`)
    );

    const diamondShapes = diamondShapeMatch
      ? diamondShapeMatch[1]
          .split(",")
          .map((s) => s.trim())
          .filter((s) => s)
      : [];

    const diamondCounts = totalDiamondsMatch
      ? totalDiamondsMatch[1]
          .split(",")
          .map((s) => s.trim())
          .filter((s) => s)
          .map((c) => parseInt(c))
      : [];

    const diamondWeights = diamondWeightMatch
      ? diamondWeightMatch[1]
          .split(",")
          .map((s) => s.trim())
          .filter((s) => s)
          .map((w) => parseFloat(w))
      : [];

    const goldWeight = goldWeightMatch ? parseFloat(goldWeightMatch[1]) : 0;

    const diamonds = diamondShapes.map((shape, i) => ({
      shape,
      weight: diamondWeights[i] || 0,
      count: diamondCounts[i] || 0,
    }));

    // Calculate price
    const result = await calculateFinalPrice({
      diamonds,
      goldWeight,
      goldKarat: selectedKarat,
    });

    const price = result.totalPrice;

    // Cache the result
    priceCache.set(cacheKey, price);

    return price;
  } catch (error) {
    console.error("Error calculating price:", error);
    return 0;
  }
};

/**
 * Calculate prices for multiple products in batches
 */
export const calculateProductPricesBatch = async (
  products,
  selectedKarat = "10K",
  batchSize = 5
) => {
  const results = [];

  // Process in batches to avoid overwhelming the system
  for (let i = 0; i < products.length; i += batchSize) {
    const batch = products.slice(i, i + batchSize);
    const batchResults = await Promise.all(
      batch.map((product) =>
        calculateProductPrice(product.description, selectedKarat)
      )
    );
    results.push(...batchResults);

    // Optional: Add a small delay between batches if needed
    // await new Promise(resolve => setTimeout(resolve, 50));
  }

  return results;
};

/**
 * Extract diamond details from description
 */
export const extractDiamondDetails = (description) => {
  const caratMatch = description.match(/Total Diamond Carat:\s*([\d.]+)/);
  const qualityMatch = description.match(/Diamond Quality:\s*([^\n]+)/);
  const shapeMatch = description.match(/Diamond Shape:\s*([^\n]+)/);
  const countMatch = description.match(/Total Diamonds:\s*(\d+)/);

  return {
    carat: caratMatch?.[1] || "",
    quality: qualityMatch?.[1]?.trim() || "",
    shape: shapeMatch?.[1]?.trim() || "",
    count: countMatch?.[1] || "",
  };
};

/**
 * Clear the price cache (useful for refreshing data)
 */
export const clearPriceCache = () => {
  priceCache.clear();
};
